.admin__sect {
  margin-top: 150px;
  justify-content: center;
  align-items: center;
}
.section6__otadiv {
  padding: 20px;
  text-align: center;
  align-items: center;
  margin: 0 auto;
  border-radius: 10px;
  border: 3px solid rgb(173, 6, 6);
  width: 600px;
}
.section6__otadiv__text__h1 {
  font-weight: 900;
  color: rgb(173, 6, 6);
}
.section6__otadiv__text__p {
  color: rgb(83, 82, 82);
  font-weight: 700;
}
.section6__otadiv__input__btn1 {
  margin-top: 20px;
  border-radius: 50%;
  background-color: green;
  border: none;
  padding-top: 1px;
  width: 30px;
  height: 30px;
}
.section6__otadiv__input__btn2 {
  margin-top: 20px;
  border-radius: 50%;
  padding-top: 1px;
  width: 30px;
  height: 30px;
  border: none;
  background-color: #fff;
  background-color: red;
}
.section6__otadiv__input1 {
  text-align: center;
  width: 400px;
  height: 40px;
  border-radius: 10px;
  border: 2px solid rgb(173, 6, 6);
}
